.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.bigred {
  font-family:"Courier New";
  font-style: italic;
  font-size: xx-large;
  color: red;
  }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 0px 0px 0px 5px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-left: none;
  border-right: none;
  border-bottom: solid 2px;
  border-bottom-color: #0d47a1;
  border-top: none;
  margin-bottom: 2vh;
  }
  .react-autosuggest__input:focus {
  outline: none;
  }

  .lamp {
    height: 3rem;
    width: 3rem;
    border-style: solid;
    border-width: 2px;
    border-radius: 50%;
    }
    .light {
    border: solid;
    width: 4.25rem;
    }

    .flex-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
      align-items: center;
      }

      .scenario-container {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-top: 2em;
        }
        .scenario-message {
        color: white;
        width: 80%;
        margin: 2px;
        padding: 1em;
        font-size: smaller;
        font-weight: bold;
        }

        .MessagesList {
          margin-top: 2px;
          margin-bottom: 2px;
          box-sizing: border-box;
          height: calc(80vh - 70px);
          overflow-y: hidden;
          overflow-x: hidden;
          }

          .MessageBubble {
            font-family: Arial, Helvetica, sans-serif;
            position: relative;
            padding: 5px;
            margin-bottom: -1em;
            width: 80%;
            border-radius: 10px;
            color: white;
            }
